import { Component, OnDestroy, ChangeDetectorRef, Input } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';


@Component({
  selector: 'pxt-header',
  // templateUrl: './pxt-app-header.component.html',
 /*
  template: `<div class="example-container" [class.example-is-mobile]="mobileQuery.matches" *ngIf="shouldRun">
 <mat-toolbar color="primary" class="example-toolbar">
   <button mat-icon-button (click)="snav.toggle()"><mat-icon>menu</mat-icon></button>
   <h1 class="example-app-name">Responsive App</h1>
 </mat-toolbar>

 <mat-sidenav-container class="example-sidenav-container"
                        [style.marginTop.px]="mobileQuery.matches ? 56 : 0">
   <mat-sidenav #snav [mode]="mobileQuery.matches ? 'over' : 'side'"
                [fixedInViewport]="mobileQuery.matches" fixedTopGap="56">
     <mat-nav-list>
       <a mat-list-item routerLink="." *ngFor="let nav of fillerNav">{{nav}}</a>
     </mat-nav-list>
   </mat-sidenav>

   <mat-sidenav-content>
     <p *ngFor="let content of fillerContent">{{content}}</p>
   </mat-sidenav-content>
 </mat-sidenav-container>
</div>

<div *ngIf="!shouldRun">Please open on Stackblitz to see result</div>`,
*/

template: `<mat-toolbar color="primary" class="example-toolbar">
              <button mat-icon-button><mat-icon>menu</mat-icon></button>
              <h1 class="example-app-name">{{pxtAppTitle}}</h1>
            </mat-toolbar>`,

  styles: [`.example-is-mobile .example-toolbar {
              position: fixed;
              z-index: 2;
            }
            h1.example-app-name {
              margin-left: 8px;
            }
            .mat-toolbar.mat-primary {
              background: #F4511E !important;
              color: #fff;
            }`]
})

export class PxtAppHeaderComponent implements OnDestroy {

  
  @Input() pxtAppTitle: String = "";
  mobileQuery: MediaQueryList;
  /*
  fillerNav = Array.from({ length: 50 }, (_, i) => `Nav Item ${i + 1}`);
  fillerContent = Array.from({ length: 50 }, () =>
    `Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut
       labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
       laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in
       voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat
       cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.`);
*/
  private _mobileQueryListener: () => void;

  constructor(changeDetectorRef: ChangeDetectorRef, media: MediaMatcher) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }
  shouldRun = true;
}

